<template>
<div class="personal-information-wrapper container-fluid" v-if="personalInfo">
    <div class="row m-0 personal-info-row-content">
        <div class="avatar-border"></div>
    </div>
    <div class="row m-0 personal-info-row-content">
        <TextInput :name="'name'" isRequired :label="$t('Joining.Name')" :wrapperClass="'w-100'" v-model="personalInfo.userNm" />
    </div>
    <div class="row m-0 personal-info-row-content">
        <PhoneInput :label="$t('Joining.Phone')" :wrapperClass="'w-100'" :placeholder="''" :name="'phoneNum'" v-model="personalInfo.phoneNum" :defaultCountry="personalInfo.phoneNum.regionCode" />
    </div>
    <div class="row m-0 personal-info-row-content">
        <TextInput :name="'email'" :label="$t('Joining.Email')" :wrapperClass="'w-100'" v-model="personalInfo.userId" />
    </div>
    <div class="row m-0 personal-info-row-content">
        <DateTimeInput isRequired :label="$t('Joining.DOB')" v-model="personalInfo.birth" :changeDob="changeDob"></DateTimeInput>
    </div>
    <div class="row m-0 personal-info-row-content">
        <RadioInput isRequired :label="$t('Joining.Country')" v-model="personalInfo.country" :options="[{name: $t(`Joining.${$CYconstants.COUNTRIES.KOREA}`), value: $CYconstants.COUNTRY_CODE.KOREA}, {name: $t(`Joining.${$CYconstants.COUNTRIES.OTHER}`), value: $CYconstants.COUNTRY_CODE.OTHER}]" :name="'countrySelector'"></RadioInput>
    </div>
    <div class="row m-0 personal-info-row-content">
        <TextInput :name="'address1'" isRequired :label="$t('Joining.Address1')" :wrapperClass="'w-100'" v-model="personalInfo.addr" />
    </div>
    <div class="row m-0 personal-info-row-content">
        <TextInput :name="'address2'" isRequired :label="$t('Joining.Address2')" :wrapperClass="'w-100'" v-model="personalInfo.addrDetail" />
    </div>
    <div class="row m-0 personal-info-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.Password')" />
        </div>
        <div class="col-12 p-0" @click="changePwd">
            <LightButton default :label="$t('Personal.ChangePassword')" />
        </div>
    </div>
    <div class="row m-0 personal-info-row-content">
        <Gap :height="'2rem'" />
        <Button :label="$t('Joining.SAVE')" :wrapperClass="'w-100'" @click.native="savePersonalInfo"></Button>
    </div>
</div>
</template>

<script>
import TextInput from '@/components/Common/Input/TextInput';
import PhoneInput from '@/components/Common/Input/PhoneInput';
import DateTimeInput from '@/components/Common/Input/DateTimeInput';
import RadioInput from '@/components/Common/Input/RadioInput';
import InputLabel from '@/components/Common/Input/InputLabel';
import Button from '@/components/Common/Button/Button';
import LightButton from '@/components/Common/Button/LightButton';
import Gap from '@/components/Common/Space/Gap';
import moment from 'moment';
import{
    findCountry
} from '@/validator/phoneCode';
import {
    UPDATE_PERSONAL_INFO
} from '@/mutations/Auth.mutation'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'

export default {
    name: 'PersonalInfo',
    components: {
        TextInput,
        PhoneInput,
        DateTimeInput,
        RadioInput,
        InputLabel,
        Button,
        LightButton,
        Gap,
    },
    data: function () {
        return {
            personalInfo: null
        }
    },
    async created() {
        this.personalInfo = JSON.parse(localStorage.getItem('user'));
        let country = findCountry(this.personalInfo.countryNum);
        this.personalInfo.phoneNum = {
            country: {
                dialCode: this.personalInfo.countryNum
            },
            regionCode:country.iso2.toUpperCase(),
            number: {
                national: this.personalInfo.userTel
            },
        };
    },
    methods: {
        changePwd() {
            return this.$router.push('/change-password')
        },
        changeDob(val) {
            this.personalInfo.birth = val
        },
        async savePersonalInfo() {
            try {
                let params = {
                    userId: this.personalInfo.userId,
                    userNm: this.personalInfo.userNm,
                    birth: this.personalInfo.birth,
                    birth: this.personalInfo.birth,
                    country: this.personalInfo.country == this.$CYconstants.COUNTRY_CODE.KOREA ? this.$CYconstants.COUNTRY_CODE.KOREA : this.$t('Personal.Other'),
                    userTel: this.personalInfo.phoneNum.number.national.replace(/ /g, ''),
                    countryNum: this.personalInfo.phoneNum.country.dialCode,
                    addrDetail: this.personalInfo.addrDetail,
                    addr: this.personalInfo.addr
                }

                await this.$store.dispatch("UPDATE_PERSONAL_INFO", params);
                this.$store.dispatch(SHOW_POPUP_BUTTON, this.$t('Personal.UPDATE_SUCCESS'));
            } catch (error) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.message);
            }
        }
    }
}
</script>
