<template>
<div class="d-flex flex-column text-input-wrapper" :class="wrapperClass">
    <label class="text-input-label" ref="anchorInput">{{label}}<span class="text-danger">{{isRequired ? ' *':''}}</span></label>
    <vue-tel-input ref="telInput" :mode="'national'"
        :defaultCountry="defaultCountry"
        :enabledFlags="false" :enabledCountryCode="true" 
        :value="value.number.national" :placeholder="placeholder" 
        :validCharactersOnly="true" :inputClasses="'text-input'" 
        :disabledFetchingCountry="false"
        :autocomplete="'off'"
        :preferredCountries="['kr', 'us', 'gb', 'vn']" @input="changeLocal"></vue-tel-input>
    <div class="text-danger error">{{error}}</div>
</div>
</template>

<script>
import {
    COUNTRY_TEL_CODE
} from '@/constants'

export default {
    name: 'PhoneInput',
    props: {
        label: String,
        value: Object,
        error: String,
        placeholder: String,
        wrapperClass: String,
        inputClasses: {
            type: String,
            default: 'phone-input'
        },
        isRequired: {
            type: Boolean,
            default: false,
        },
        defaultCountry: String,
    },
    model: {
        event: 'change'
    },
    data: function () {
        return {
            phoneObject: {},
        }
    },
    methods: {
        changeLocal: function (value, phoneObject) {
            this.phoneObject = {
                ...phoneObject
            };
            this.$emit('change', {
                ...phoneObject
            });
        },
    },
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/variables.scss';

.text-input-wrapper {
    .text-input-label {
        font-family: NotoSansKR;
        font-weight: 300;
        font-size: 1.4rem;
        line-height: 2rem;
        color: #7C848D;
        margin-bottom: .7rem;
    }

    .error {
        display: inline-block;
        font-size: 1.2rem;
        line-height: 2.0rem;
        width: 100%;
        margin-bottom: .3rem;
        min-height: 2.0rem;
        position: relative;
    }

    .vue-tel-input {
        border-bottom: 0.05rem solid #E0E0E0 !important;
        padding-bottom: 0.05rem;

        .text-input {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 1.4rem;
            line-height: 2.0rem;
            color: #4f4f4f;
            outline: none;
            border: none;
            border-bottom: 0.05rem solid #E0E0E0;
            padding-bottom: .5rem;
            position: relative;
            top: -0.05rem;

            &:focus {
                color: $cy-blue;
                border-bottom: 0.05rem solid $cy-blue;
            }

            &::placeholder {
                font-family: NotoSansKR;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 20px;
                color: #4F4F4F;
                opacity: 0.5;
            }
        }
    }

    .vue-tel-input:focus-within {
        border-bottom: 0.05rem solid $cy-blue !important;
        color: $cy-blue !important;
    }
}
</style>
